import {SessionStore} from '@phinxlab/libby-core-public';
const myStorage = window.localStorage;

export class SessionStoreWeb extends SessionStore {

    async retrieve() {
        return (myStorage.getItem('libby-token'));
    }

    clean() {
        myStorage.removeItem('libby-token');
    }

    async persist(token) {
        myStorage.setItem('libby-token',token);
    }

    static create() {
        return new SessionStoreWeb();
    }

}